.new-res-med{
    background-color: #ffffff;
    padding: 10px;
    box-shadow: 2px 2px 5px #333;
}

.title{
    text-align: center;
}
.new-res-med{
    /* width: 700px; */
    margin: 10px;
}
.new-res-med-data {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.new-res-med-data > div{
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.new-res-med-btn{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    column-gap: 20px;
    margin-top: 20px;
}
div > b{
    margin: 20px;

}


.new-user-card{
    padding: 10px 20px;
    margin: 10px;
    background-color: rgb(255, 255, 255);
    box-shadow: 2px 2px 5px #333;
}
.new-user-line{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.new-contact-card{
    padding: 30px 20px;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border-bottom: solid 1px #666;
}
.new-temoignage-card > div,
.new-partenaire-card >div,
.new-contact-card > div {
    flex:1;
    min-width: 300px;
}
.new-partenaire-card{
    padding: 30px 20px;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: solid 1px #666;
}
.new-contact-line{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}
.new-temoignage-card{
    padding: 30px 20px;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: solid 1px #666;
}
.main{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.main > div{
    flex: 1;
}
.a{
    background-color: #cfcfcf;
}

.home-div{
    display: block;
    width: 90%;
    margin: 10px auto;
}

@media (width > 1200px){
    .home-div{
        width: 80%;
    }
}