.form-input{
    margin: 10px;
}


.admin-home-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.deleter-container{
    position: relative;
}

.deleter-absolute{
    position: absolute;
    background-color: #33333333;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 0px;
    top: 0px;
    right: 0px;
    left: 0px;
    flex-direction: column;
    z-index: 50;
}

.deleter-absolute > div {
    padding: 8px 13px;
    background-color: red;
    margin: 5px;
}
.deleter-absolute > div:hover{
    cursor: pointer;
    opacity: 70%;
}
input{
    margin:10px 30px;
}
.form{
    margin: 30px;
    background-color: white;
    padding: 20px;
    border: solid 2px black;
}
.touchable:hover {
    cursor: pointer;
}